body {
  margin: 0;
  padding: 0;
  font-family: Roboto, Arial;
}

.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 30px 15px 0;
}

.clearfix * {
  zoom: 1;
}

.clearfix:before,
.clearfix:after{
  display: table;
  content: "";
  line-height: 0;
}

.clearfix:after {
  clear: both;
}

.add-product {
  min-height: 200px;
  margin-bottom: 10px;
}

.add-product__left {
  float: left;
  width: 100%;
  margin-right: -110px;
  padding-right: 110px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.add-product__right {
  float: left;
  width: 110px;
}

.add-product__group {
  float: left;
  width: 50%;
  padding-right: 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.add-product__btn {
  margin-top: 27px;
}

.discount-field {
  margin: 0 15px;
}

.table-container {
  margin-bottom: 50px;
}

.table-cell {
  padding-left: 0 !important;
}

.discount-btn {
  margin-left: 30px;
}


